.veilarbvedtaksstottefs {
	.dialog-section-minified {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;

		padding: 0.5rem;

		&__dialog-img {
			width: 24px;
			height: 24px;

			margin: 1rem auto 0 auto;
		}
	}

	.dialog-section__toggle_btn {
		height: 4rem;
		width: 4rem;
		border-radius: 0;
		border-right: 1px solid var(--a-border-divider);
		border-bottom: 1px solid var(--a-border-divider);

		svg {
			font-size: 2.5rem;
		}
	}

	.dialog-section-header-minified {
		height: 4rem;
		border-bottom: 1px solid var(--a-border-divider);
	}
}
