@import '@navikt/ds-css' layer(aksel);

.veilarbvedtaksstottefs {
	/* Veilarbpersonflatefs setter display: flex på elementet ovenfor slik at vi kan bruke den resterende høyden */
	flex-grow: 1;
	background-color: var(--a-bg-subtle);
}

/* Må fjernes/justeres når vi oppdaterer til navds@v6 */
